import ImageGallery from "react-image-gallery";
import "react-image-gallery/styles/css/image-gallery.css";
import useMediaQuery from "@mui/material/useMediaQuery";
import Box from "@mui/material/Box";
import CloseIcon from "@mui/icons-material/Close";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import {
  ModalList,
  ModalListItem,
  ModalLinkButton,
  BoxWrapper,
} from "./ModalForFirstItem.styled";
import image1 from "../../images/modalThird/image.jpg";
import image2 from "../../images/modalThird/image1.jpg";
import image3 from "../../images/modalThird/image2.jpg";
import image4 from "../../images/modalThird/image3.jpg";

export const ModalWindowForThirdItem = ({
  openThirdModal,
  handleCloseThirdModal,
}) => {
  const TabletMediaWidth = useMediaQuery("(min-width:768px)");
  const DesktopMediaWidth = useMediaQuery("(min-width:1024px)");

  const images = [
    {
      original: image1,
    },
    {
      original: image2,
    },
    {
      original: image3,
    },
    {
      original: image4,
    },
  ];

  return (
    <Modal
      open={openThirdModal}
      onClose={handleCloseThirdModal}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <Box
        sx={[
          {
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            width: 350,
            bgcolor: "#ffffff",
            outline: "none",
            padding: "25px",
            color: "#303030",
          },
          TabletMediaWidth && {
            width: 700,
            display: "flex",
            alignItems: "center",
          },
          DesktopMediaWidth && {
            width: 1000,
          },
        ]}
      >
        <BoxWrapper>
          <CloseIcon
            fontSize="large"
            sx={[
              {
                cursor: "pointer",
                color: "#000000",
                marginLeft: "300px",
                marginTop: "10px",
                marginBottom: "15px",
              },
              TabletMediaWidth && {
                marginLeft: "675px",
                marginTop: "0px",
              },
              DesktopMediaWidth && {
                marginLeft: "480px",
                marginBottom: "28px",
                width: 1000,
              },
            ]}
            onClick={handleCloseThirdModal}
          />
          <ModalList>
            <ModalListItem>
              <Typography
                variant="body1"
                sx={[
                  {
                    fontFamily: "inherit",
                    fontSize: "12px",
                    letterSpacing: "0.03em",
                  },
                  TabletMediaWidth && {
                    fontSize: "14px",
                  },
                  DesktopMediaWidth && {
                    fontSize: "16px",
                  },
                ]}
              >
                  GARMIN ETREX 32X
                <br />
                : 11 000 .
                <br />
                <br />
                :
                <br />- 2,2-     
                240 x 320      -
                 .
                <br />
                -    TopoActive   
                      .
                <br />-    GPS   
                     ,    GPS.
                <br />
                <br /> :
                <br />    ,      .
              </Typography>
            </ModalListItem>
            <ModalListItem>
              <Typography
                variant="body1"
                sx={[
                  {
                    fontFamily: "inherit",
                    fontSize: "12px",
                    letterSpacing: "0.03em",
                    color: "#ff6c00",
                  },
                  TabletMediaWidth && {
                    fontSize: "14px",
                  },
                  DesktopMediaWidth && {
                    fontSize: "16px",
                  },
                ]}
              >
                  Garmin eTrex 32x  
                  TopoActive.   
                   '   , 
                  GPS  GLONASS    
                ,    .  eTrex 32x
                 3-    .
              </Typography>
            </ModalListItem>
          </ModalList>
          <ModalLinkButton href="https://t.me/LendLease_Ukraine">
            
          </ModalLinkButton>
        </BoxWrapper>
        <Box
          sx={[
            {
              display: "none",
            },
            TabletMediaWidth && {
              display: "block",
              marginLeft: "30px",
            },
          ]}
        >
          {" "}
          <ImageGallery
            showPlayButton={false}
            showBullets={true}
            showThumbnails={false}
            items={images}
          />
        </Box>
      </Box>
    </Modal>
  );
};

